<template>
    <div id="web_editor">
        <button
            type="button"
            class="btn btn-primary btn-sm waves-effect waves-light web_editor_save"
            @click="saveEditor"
        >儲存</button>
        <div class="row web_editor_row">
            <div class="col-9 edit_area">
                <div class="edit_area_block">
                    <div class="block-content">
                        <!-- 編輯區塊 -->
                        <draggable
                            class="dragArea tinymce-editor"
                            :list="defaultContents"
                            group="editor"
                            @change="log"
                            @choose="onChoose"
                            @unchoose="onUnchoose"
                        >
                            <div
                                v-for="(content, index) in contents"
                                :key="index"
                                @dblclick="editContent(index)"
                            >
                                <button
                                    type="button"
                                    class="close"
                                    aria-label="Close"
                                    @click="removeAt(index)"
                                    v-if="content.editorEnable"
                                >
                                    <span aria-hidden="true">×</span>
                                </button>
                                <editor
                                    api-key="zmpxfb6q25nhcjo2zi5camd5ntjrkh46mllcpab45h474fa0"
                                    :init="{
                                        menubar: false,
                                        plugins: [
                                        'advlist autolink lists link image charmap print preview anchor',
                                        'searchreplace visualblocks code fullscreen',
                                        'insertdatetime media table paste code help wordcount',
                                        ],
                                        toolbar: content.toolbar,
                                        content_css: [ cbData.theme ],
                                    }"
                                    v-model="defaultContents[index].content"
                                    :inline="true"
                                    :disabled="!content.editorEnable"
                                    @onBlur="onBlur"
                                />
                            </div>
                        </draggable>
                    </div>
                </div>
            </div>
            <div class="col-3 side_element">
                <div class="side_element_block">
                    <div class="block-header has-border">
                        <h3>
                            <strong>插入段落</strong>
                        </h3>
                    </div>
                    <div class="block-content tinymce-editor">
                        <!-- 功能區塊 -->
                        <draggable
                            class="dragArea"
                            :list="insertContents"
                            :group="{ name: 'editor', pull: 'clone', put: false }"
                            :clone="onClone"
                            :sort="false"
                            @change="log"
                            @choose="onChoose"
                            @unchoose="onUnchoose"
                        >
                            <div
                                r
                                v-for="(content, index) in insertContents"
                                :key="index"
                                v-html="content.content"
                            ></div>
                        </draggable>
                        <!-- 插入圖片 -->
                        <!-- <button
                            type="button"
                            class="btn btn-primary btn-block"
                            data-toggle="modal"
                            data-target="#imageModal"
                        >插入圖片</button>-->
                        <!-- 插入圖片 Modal -->
                        <div class="modal fade" id="imageModal">
                            <div class="modal-dialog modal-xl modal-dialog-centered">
                                <div class="modal-content">
                                    <!-- Modal Header -->
                                    <div class="modal-header">
                                        <h4 class="modal-title">選擇圖片</h4>
                                        <button
                                            type="button"
                                            class="close"
                                            data-dismiss="modal"
                                        >&times;</button>
                                    </div>

                                    <!-- Modal body -->
                                    <div class="modal-body">call api 獲取圖片</div>

                                    <!-- Modal footer -->
                                    <div class="modal-footer">
                                        <button
                                            type="button"
                                            class="btn btn-primary"
                                            data-dismiss="modal"
                                            @click="insertImage"
                                        >插入圖片</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <pre>{{ JSON.stringify(this.contents, null, 2) }}</pre>
    </div>
</template>

<script>
import Editor from "@tinymce/tinymce-vue";
import draggable from "vuedraggable";

export default {
    name: "web_editor",
    components: {
        editor: Editor,
        draggable
    },

    data() {
        return {
            // 外部傳入參數
            cbData: {},
            insertContents: [
                {
                    type: "title",
                    content: `<h3 class="block-title">活動介紹</h3>`
                },
                {
                    type: "title",
                    content: `<h4>H4 Title</h4>`
                },
                {
                    type: "title",
                    content: `<h5>H5 Title</h4>`
                },
                {
                    type: "text",
                    content: `<div>
                  <p>text</p>
                </div>`
                },
                {
                    type: "text",
                    content: `<ul>
                        <li>111</li>
                        <li>222</li>
                    </ul>`
                },
                {
                    type: "text",
                    content: `<ol>
                        <li>111</li>
                        <li>222</li>
                    </ol>`
                },
                {
                    type: "table",
                    content: ` <div
                                    class="table-responsive"
                                >
                                    <table class="table">
                                        <thead class="grey lighten-3">
                                            <tr>
                                                <th
                                                    style="width: 50px;"
                                                    class="text-center"
                                                >#</th>
                                                <th
                                                    class="text-center"
                                                >1</th>
                                                <th
                                                    class="text-center"
                                                >2</th>
                                                <th
                                                    class="text-center"
                                                >3</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr
                                                class="text-center"
                                            >
                                                <td>1</td>
                                                <td 
                                                    class="text-center"
                                                >aaa</td>
                                                    <td 
                                                    class="text-center"
                                                >aaa</td>
                                                    <td 
                                                    class="text-center"
                                                >aaa</td>
                                            </tr>
                                            
                                        </tbody>
                                    </table>
                                </div>`
                }
            ],
            defaultContents: [
                {
                    type: "title",
                    content: `<h1 >The world&rsquo;s first rich text editor in the cloud</h1>`
                },
                {
                    type: "table",
                    content: ` <div
                                    class="table-responsive"
                                >
                                    <table class="table">
                                        <thead class="grey lighten-3">
                                            <tr>
                                                <th
                                                    style="width: 50px;"
                                                    class="text-center"
                                                >#</th>
                                                <th
                                                    class="text-center"
                                                >111</th>
                                                <th
                                                    class="text-center"
                                                >222</th>
                                                <th
                                                    class="text-center"
                                                >333</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr
                                                class="text-center"
                                            >
                                                <td>1</td>
                                                <td 
                                                    class="text-center"
                                                >aaa</td>
                                                    <td 
                                                    class="text-center"
                                                >aaa</td>
                                                    <td 
                                                    class="text-center"
                                                >aaa</td>
                                            </tr>
                                            
                                        </tbody>
                                    </table>
                                </div>`
                },
                {
                    type: "text",
                    content: `<div>
                  <p>Have you heard about Tiny Cloud? It&rsquo;s the first step in our journey to help you deliver great content creation experiences, no matter your level of expertise. 50,000 developers already agree. They get free access to our global CDN, image proxy services and auto updates to the TinyMCE editor. They&rsquo;re also ready for some exciting updates coming soon.</p>
                  <p>One of these enhancements is <strong>Tiny Drive</strong>: imagine file management for TinyMCE, in the cloud, made super easy. Learn more at <a href="../../../tinydrive/">tiny.cloud/tinydrive</a>, where you&rsquo;ll find a working demo and an opportunity to provide feedback to the product team.</p>
                </div>`
                },
                {
                    type: "title",
                    content: `<h2 >An editor for every project</h2>`
                },
                {
                    type: "text",
                    content: `<div>
              <p>Here are some of our customer&rsquo;s most common use cases for TinyMCE:</p>
              <ul>
              <li>Content Management Systems (<em>e.g. WordPress, Umbraco</em>)</li>
              <li>Learning Management Systems (<em>e.g. Blackboard</em>)</li>
              <li>Customer Relationship Management and marketing automation (<em>e.g. Marketo</em>)</li>
              <li>Email marketing (<em>e.g. Constant Contact</em>)</li>
              <li>Content creation in SaaS systems (<em>e.g. Eventbrite, Evernote, GoFundMe, Zendesk</em>)</li>
              </ul>
              <p>And those use cases are just the start. TinyMCE is incredibly flexible, and with hundreds of APIs there&rsquo;s likely a solution for your editor project. If you haven&rsquo;t experienced Tiny Cloud, get started today. You&rsquo;ll even get a free trial of our premium plugins &ndash; no credit card required!</p>
            </div>`
                }
            ],
            toastrOptions: {
                closeButton: true,
                debug: false,
                positionClass: "md-toast-top-center",
                onclick: null,
                showDuration: "1000",
                hideDuration: "1000",
                timeOut: "10000",
                extendedTimeOut: "3000",
                showEasing: "swing",
                hideEasing: "linear",
                showMethod: "fadeIn",
                hideMethod: "fadeOut"
            }
        };
    },
    computed: {
        contents() {
            // 不同 type 使用不同 toolbar
            return this.defaultContents.map(item => {
                const newItem = {
                    ...item
                };
                if (newItem.type == "title") {
                    // title
                    newItem.toolbar = "undo redo | bold italic underline";
                } else if (newItem.type == "text") {
                    // text
                    newItem.toolbar = [
                        "undo redo | bold italic underline | fontselect fontsizeselect",
                        "forecolor backcolor | alignleft aligncenter alignright alignfull | numlist bullist outdent indent"
                    ];
                } else if (newItem.type == "table") {
                    // table
                    newItem.toolbar =
                        "table tabledelete | tableprops tablerowprops tablecellprops | tableinsertrowbefore tableinsertrowafter tabledeleterow | tableinsertcolbefore tableinsertcolafter tabledeletecol";
                } else if (newItem.type == "image") {
                    // image
                    newItem.toolbar = "undo redo | image";
                }
                return newItem;
            });
        }
    },
    mounted() {
        toastr.options = this.toastrOptions;

        const el = document.querySelector("#cbdata");
        this.cbData = JSON.parse(el.dataset.cbdata);

        // 取得資料
        this.getContent();
    },
    methods: {
        removeAt(idx) {
            this.defaultContents.splice(idx, 1);
        },
        log(evt) {
            console.log("log", evt);
        },
        onChoose() {
            // console.log("choose");
        },
        onUnchoose() {
            // console.log("unchoose");
        },
        onBlur() {
            // console.log("onblur");
            setTimeout(() => {
                this.closeEdit();
            }, 100);
        },
        onClone(evt) {
            // 改寫 clone 為 deep copy 修改值才不會變更原本的
            return { ...evt };
        },
        editContent(idx) {
            console.log(idx);
            // this.closeEdit();
            this.$set(this.defaultContents[idx], "editorEnable", true);
        },
        closeEdit() {
            this.defaultContents.forEach((item, idx) => {
                this.$set(this.defaultContents[idx], "editorEnable", false);
            });
        },
        insertImage() {
            this.defaultContents.push({
                type: "image",
                content: `<img src="https://static.toiimg.com/thumb/msid-67586673,width-800,height-600,resizemode-75,imgsize-3918697,pt-32,y_pad-40/67586673.jpg" alt="">`
            });
        },
        getContent() {
            fetch(
                this.cbData.api +
                    `applySn/${this.cbData.applySn}/languageKey/${this.cbData.languageKey}`
            )
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(data => {
                    if (data.data) {
                        this.defaultContents = data.data;
                    }
                });
        },
        saveEditor() {
            let formData = new FormData();

            formData.append("applySn", this.cbData.applySn);
            formData.append("languageKey", this.cbData.languageKey);
            formData.append("data", JSON.stringify(this.defaultContents));

            fetch(this.cbData.api, {
                method: "POST",
                body: formData
            })
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(res => {
                    if (res.success) {
                        toastr["success"]("Success", "儲存成功");
                    } else {
                        toastr["error"]("Error", res.message);
                    }
                });
        }
    }
};
</script>
<style lang="scss">
#cbdata {
    display: none;
}

.tinymce-editor img {
    max-width: 100%;
    height: auto;
}

.tinymce-editor *[contentEditable="true"]:focus,
.tinymce-editor *[contentEditable="true"]:hover {
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem #007bff40;
    border-radius: 0.25rem;
}
</style>
