import Vue from "vue";
import WebEditor from "./components/WebEditor.vue";

Vue.config.productionTip = false;

new Vue({
    render: function(h) {
        const app = h(WebEditor);
        const el = document.querySelector("#web_editor");
        const cbdata = h("span", {
            attrs: {
                id: "cbdata",
                "data-cbdata": el.dataset.cbdata,
            },
        });

        return h("div", [app, cbdata]);
    },
}).$mount("#web_editor");